import React from 'react';
import { Route, Switch, Redirect } from 'react-router-dom';
import { RequireNoAuth, RequirePermission } from '../middlewares/HOC';
import getPaths from './PATHS';

import {
    LandginHome,
    Signin, ResetPassword, Activation,
    AdminDashboard,

    AdminAdmins, AdminCompanies, AdminCompanyTypes, AdminRoles, AdminClients,
    AdminPackages, AdminPromotions, AdminSubscriptions,
    AdminSystemSettings, AdminAbout, AdminContacts, AdminTerms, AdminPrivacy, AdminFAQ, AdminComplaints,
    AdminCategories, AdminBrands, AdminProducts, AdminOrders, AdminOffers, AdminAds,
    AdminPayments, AdminStats, AdminUsage, AdminCategoriesReport, AdminBrandsReport, AdminCompanyTypesReport,


} from '../pages';

const Router = () => {
    const PATHS = getPaths();
    return <Switch>
        {/* Basic Routes */}
        <Route path={PATHS.HOME.URL} exact component={LandginHome} />
        <Route path={PATHS.RESET_PASSWORD.URL} exact component={ResetPassword} />
        <Route path={PATHS.ACTIVATION.URL} exact component={Activation} />

        {/* <Route path="/signup" exact component={RequireNoAuth(Signup)} /> */}
        <Route path={PATHS.SIGNIN.URL} exact component={RequireNoAuth(Signin)} />
        {/* <Route path="/profile" exact component={Profile} /> */}



        <Route path={PATHS.ADMIN_DASHBOARD.URL} exact component={RequirePermission(AdminDashboard, PATHS.ADMIN_DASHBOARD.RESOURCES)} />

        <Route path={PATHS.ADMIN_ADMINS.URL} exact component={RequirePermission(AdminAdmins, PATHS.ADMIN_ADMINS.RESOURCES)} />
        <Route path={PATHS.ADMIN_ROLES.URL} exact component={RequirePermission(AdminRoles, PATHS.ADMIN_ROLES.RESOURCES)} />

        <Route path={PATHS.ADMIN_COMPANIES_TYPES.URL} exact component={RequirePermission(AdminCompanyTypes, PATHS.ADMIN_COMPANIES_TYPES.RESOURCES)} />
        <Route path={PATHS.ADMIN_COMPANIES.URL} exact component={RequirePermission(AdminCompanies, PATHS.ADMIN_COMPANIES.RESOURCES)} />
        <Route path={PATHS.ADMIN_CLIENTS.URL} exact component={RequirePermission(AdminClients, PATHS.ADMIN_CLIENTS.RESOURCES)} />


        <Route path={PATHS.ADMIN_PACKAGES.URL} exact component={RequirePermission(AdminPackages, PATHS.ADMIN_PACKAGES.RESOURCES)} />
        <Route path={PATHS.ADMIN_PROMOTIONS.URL} exact component={RequirePermission(AdminPromotions, PATHS.ADMIN_PROMOTIONS.RESOURCES)} />
        <Route path={PATHS.ADMIN_SUBSCRIPTIONS.URL} exact component={RequirePermission(AdminSubscriptions, PATHS.ADMIN_SUBSCRIPTIONS.RESOURCES)} />


        <Route path={PATHS.ADMIN_SYSTEM_SETTINGS.URL} exact component={RequirePermission(AdminSystemSettings, PATHS.ADMIN_SYSTEM_SETTINGS.RESOURCES)} />
        <Route path={PATHS.ADMIN_ABOUT.URL} exact component={RequirePermission(AdminAbout, PATHS.ADMIN_ABOUT.RESOURCES)} />
        <Route path={PATHS.ADMIN_CONTACTS.URL} exact component={RequirePermission(AdminContacts, PATHS.ADMIN_CONTACTS.RESOURCES)} />
        <Route path={PATHS.ADMIN_TERMS.URL} exact component={RequirePermission(AdminTerms, PATHS.ADMIN_TERMS.RESOURCES)} />
        <Route path={PATHS.ADMIN_PRIVACY.URL} exact component={RequirePermission(AdminPrivacy, PATHS.ADMIN_PRIVACY.RESOURCES)} />
        <Route path={PATHS.ADMIN_FAQ.URL} exact component={RequirePermission(AdminFAQ, PATHS.ADMIN_FAQ.RESOURCES)} />
        <Route path={PATHS.ADMIN_COMPLAINTS.URL} exact component={RequirePermission(AdminComplaints, PATHS.ADMIN_COMPLAINTS.RESOURCES)} />


        <Route path={PATHS.ADMIN_CATEGORIES.URL} exact component={RequirePermission(AdminCategories, PATHS.ADMIN_CATEGORIES.RESOURCES)} />
        <Route path={PATHS.ADMIN_BRANDS.URL} exact component={RequirePermission(AdminBrands, PATHS.ADMIN_BRANDS.RESOURCES)} />
        <Route path={PATHS.ADMIN_PRODUCTS.URL} exact component={RequirePermission(AdminProducts, PATHS.ADMIN_PRODUCTS.RESOURCES)} />
        <Route path={PATHS.ADMIN_ORDERS.URL} exact component={RequirePermission(AdminOrders, PATHS.ADMIN_ORDERS.RESOURCES)} />
        <Route path={PATHS.ADMIN_OFFERS.URL} exact component={RequirePermission(AdminOffers, PATHS.ADMIN_OFFERS.RESOURCES)} />
        <Route path={PATHS.ADMIN_ADS.URL} exact component={RequirePermission(AdminAds, PATHS.ADMIN_ADS.RESOURCES)} />


        <Route path={PATHS.ADMIN_PAYMENTS.URL} exact component={RequirePermission(AdminPayments, PATHS.ADMIN_PAYMENTS.RESOURCES)} />
        <Route path={PATHS.ADMIN_STATS.URL} exact component={RequirePermission(AdminStats, PATHS.ADMIN_STATS.RESOURCES)} />
        <Route path={PATHS.ADMIN_USAGE.URL} exact component={RequirePermission(AdminUsage, PATHS.ADMIN_USAGE.RESOURCES)} />
        <Route path={PATHS.ADMIN_CATEGORIES_REPORT.URL} exact component={RequirePermission(AdminCategoriesReport, PATHS.ADMIN_CATEGORIES_REPORT.RESOURCES)} />
        <Route path={PATHS.ADMIN_BRANDS_REPORT.URL} exact component={RequirePermission(AdminBrandsReport, PATHS.ADMIN_BRANDS_REPORT.RESOURCES)} />
        <Route path={PATHS.ADMIN_COMPANY_TYPES_REPORT.URL} exact component={RequirePermission(AdminCompanyTypesReport, PATHS.ADMIN_COMPANY_TYPES_REPORT.RESOURCES)} />


        {/* {renderRoutes(user.roleId)} */}

        {/* Invalid Routes */}
        <Route>
            <Route path="*" exact component={LandginHome} />
            <Redirect from='*' to='/' />
        </Route>

    </Switch>;
};

export default Router;
